import { Component, OnInit } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonToolbar,
} from '@ionic/angular/standalone'
import { ActivatedRoute, Router } from '@angular/router'
import { TranslateModule } from '@ngx-translate/core'
import { ConfettiComponent } from 'src/app/components/confetti/confetti.component'

@Component({
  selector: 'app-cgu-congrats',
  templateUrl: './cgu-congrats.page.html',
  styleUrls: ['./cgu-congrats.page.scss'],
  imports: [
    IonContent,
    CommonModule,
    FormsModule,
    TranslateModule,
    ConfettiComponent,
  ],
})
export class CguCongratsPage {
  constructor(private route: ActivatedRoute, private router: Router) {}

  async ionViewWillEnter() {
    this.route.queryParams.subscribe((params) => {
      if (params['nextUrl']) {
        setTimeout(() => {
          this.router.navigate([params['nextUrl']], { replaceUrl: true })
        }, 5000)
      } else {
        setTimeout(() => {
          this.router.navigate(['/account/settings/cgu'], { replaceUrl: true })
        }, 5000)
      }
    })
  }
}
